import React, { Component } from 'react';
import './App.css';
import gitGud from './Assets/Birdwave.jpg';

class App extends Component {

  constructor() {
    super();

    this.state = {
      actionToOpenDoor: "left",
      timeRestrait: false,
      startTime: 0,
      endTime: 0,
      whiteList: false,
      systemMalfunction: "nothing",
      waitTime: 0,
      tagBuffer: [],
    };
    this.handleTimeRestriction = this.handleTimeRestriction.bind(this);
    this.handleChange = this.handleChange.bind(this);
    //this.removeTag = this.removeTag.bind(this);

  }

  insertTag = () => {
    let tag =  document.getElementById("tag").value.toUpperCase();
    var re = /[0-9A-Fa-f]{10}/g;
    var a = parseInt(tag,16);
    //a.toString(16).toUpperCase()
    console.log(a);
    if(re.test(tag) && tag.length == 10){
      this.state.tagBuffer.push([tag]);
      this.setState({
          tagBuffer: this.state.tagBuffer,
      });
      console.log(this.state.tagBuffer);
      document.getElementById("invalid").style.display = "none";
    }
    else{
      document.getElementById("invalid").style.display = "block";
      console.log("error");
    }
  }

  handleTimeRestriction(){

    var x = document.getElementById("myDIV");
    if (x.style.display === "block") {
        x.style.display = "none";
    } else {
        x.style.display = "block";
    }

    this.setState({
        timeRestrait: !this.state.timeRestrait
    });
  }

  handleChange(){
    this.setState({
      actionToOpenDoor: document.getElementById("actions").value,
      timeRestrait: this.state.timeRestrait,
      startTime: document.getElementById("appt-time_start").value,
      endTime: document.getElementById("appt-time_end").value,
      tagsRestricted: document.getElementById("tags_restriction").value
      //systemMalfunction: document.getElementById("system_malfunction").value
      //waitTime: [document.getElementById("wait_time").value,[document.getElementById("wait_time_minute").value,document.getElementById("wait_time_second").value].join(':')].join(':'),
    }, () => {
      this.generateCSV();
    });
  }

  generateCSV = () => {
    // Example data given in question text
    var data = [
      /*['Action To Open the Door', this.state.actionToOpenDoor],
      ['Time restrait', this.state.timeRestrait],
      ['Starting time', this.state.startTime],
      ['Ending time', this.state.endTime],
      ['Tag Restriction', this.state.tagsRestricted],
      ['If there is a system malfunction', this.state.systemMalfunction]*/
      //['Wait time', this.state.waitTime]
      [this.state.actionToOpenDoor],
      [this.state.timeRestrait],
      [this.state.startTime],
      [this.state.endTime],
      [this.state.tagsRestricted]
      //[this.state.systemMalfunction]
    ];

    var csvContent = '';
    data.forEach(function(infoArray, index) {
      var dataString = infoArray.join(',');
      csvContent += index < data.length ? dataString + '\n' : dataString;
    });

    var download = function(content, fileName, mimeType) {
      var a = document.createElement('a');
      mimeType = mimeType || 'application/octet-stream';

      if (navigator.msSaveBlob) { // IE10
        navigator.msSaveBlob(new Blob([content], {
          type: mimeType
        }), fileName);
      } else if (URL && 'download' in a) { //html5 A[download]
        a.href = URL.createObjectURL(new Blob([content], {
          type: mimeType
        }));
        a.setAttribute('download', fileName);
        document.body.appendChild(a);
        a.click();
        document.body.removeChild(a);
      } else {
        //location.href = 'data:application/octet-stream,' + encodeURIComponent(content); // only this mime type is supported
      }
    }

    download(csvContent, 'settings.csv', 'text/csv;encoding:utf-8');

  }


  generateTAG = () => {

    var csvContent = '';
    var bufferLength = this.state.tagBuffer.length;
    this.state.tagBuffer.forEach(function(infoArray, index) {
      var dataString = infoArray.join(',');
      csvContent += index < bufferLength ? dataString + '\n' : dataString;
    });

    var download = function(content, fileName, mimeType) {
      var a = document.createElement('a');
      mimeType = mimeType || 'application/octet-stream';

      if (navigator.msSaveBlob) { // IE10
        navigator.msSaveBlob(new Blob([content], {
          type: mimeType
        }), fileName);
      } else if (URL && 'download' in a) { //html5 A[download]
        a.href = URL.createObjectURL(new Blob([content], {
          type: mimeType
        }));
        a.setAttribute('download', fileName);
        document.body.appendChild(a);
        a.click();
        document.body.removeChild(a);
      } else {
        //location.href = 'data:application/octet-stream,' + encodeURIComponent(content); // only this mime type is supported
      }
    }

    download(csvContent, 'tags.csv', 'text/csv;encoding:utf-8');

  }

  removeTag = (index) => {
    var filteredItems = this.state.tagBuffer.slice(0, index).concat(this.state.tagBuffer.slice(index + 1, this.state.tagBuffer.length))
    this.setState({
      tagBuffer: filteredItems
    });
  }

  //M U C H  A E S T H E T I C
  makeWideText = (string) =>{
    var aesthetic = [];
    string = string.toUpperCase();
    string = string.split('');
    for(var i = 0; i < string.length; i++){
      aesthetic.push(string[i]);
      aesthetic.push(" ");
    }
    return aesthetic.join("");
  }

  render() {

    const TEXT = ["Upload existing configuration:", "Which action opens the door?:","What times of the day should the system shut off?:","","Restrict valid PIT tag codes?:", "What to do during system malfunction?:", "Wait time before the same bird can return for food:" ];
    const ACTIONS = ["Both","Left","Right","None (Always open)","None (Always closed)"];
    const TIME_RESTRICTION = ["No","Yes"];
    const TAGS_RESTRICTION = ["No (allow all)","\"Yes, White list\"","\"Yes, Black list\""];
    const SYSTEM_MALFUNCTION = ["Nothing (try function as usual)","Door always closed","Door always open"];

    const mappingFunction = i => <option key={i} value={`${i.toLowerCase()}`}>{i}</option>;
    const mappingTextFunction = i => <div key={i}>{i}</div>;
    const inputStyle = {width: '20px'};

    const tagMapping = this.state.tagBuffer.map((i,index) =>
        <div key={index}>{i}
          <button onClick={() => this.removeTag(index)}>Remove</button>
        </div>
    );

    return (
      <div>

        <div>
          <h1>Two-Action Bird Feeder Settings</h1>
          {/*<img src={gitGud}/>*/}
        </div>

        <table>
        <tbody>

          {/*<tr>
            <td>{TEXT[0]}</td>
            <td>
              <div>
                <form method="post" encType="multipart/form-data">
                    <input type="file" id="file" name="file" accept=".csv" multiple></input>
                    <button>Submit</button>
                </form>
              </div>
            </td>
          </tr>*/}

          <tr>
            <td>{TEXT[1]}</td>
            <td>
              <div>
                <form action="/action_page.php" method="get">
                  <select id="actions">{ACTIONS.map(mappingFunction)}</select>
                  <button type="reset" value="Reset">Reset</button>
                </form>
              </div>
            </td>
          </tr>

          <tr>
            <td>{TEXT[2]}</td>
            <td>
              <div>
                <label className="switch">
                  <input onChange={this.handleTimeRestriction} type="checkbox"/>
                  <span className="slider round"></span>
                </label>
                <span>Always On / Select Times</span>
              </div>
            </td>
          </tr>

          <tr>
            <td>{TEXT[3]}</td>
            <td>
              <div id="myDIV" className="control">
                <form action="/action_page.php" method="get">
                  <p>Enter the time in AM/PM format</p>
                  <label>Shut off at:</label>
                  <input type="time" id="appt-time_start" name="appt-time_start" required/>
                  <p/><label>Wake up at:</label>
                  <input type="time" id="appt-time_end" name="appt-time_end" required/>
                  <button type="reset" value="Reset">Reset</button>
                </form>
              </div>
            </td>
          </tr>

          <tr>
            <td>{TEXT[4]}</td>
            <td>
              <div>
                <form method="get">
                  <select id="tags_restriction">{TAGS_RESTRICTION.map(mappingFunction)}</select>
                  <button type="reset" value="Reset">Reset</button>
                </form>
              </div>
            </td>
          </tr>

          {/*<tr>
            <td>{TEXT[5]}</td>
            <td>
              <div>
                <form action="/action_page.php" method="get">
                  <select id="system_malfunction">{SYSTEM_MALFUNCTION.map(mappingFunction)}</select>
                  <button type="reset" value="Reset">Reset</button>
                </form>
              </div>
            </td>
          </tr>*/}

          {/*<tr>
            <td>{TEXT[6]}</td>
            <td>
              <div>
                <form action="/action_page.php" method="get">
                  <input style={inputStyle} min="0" max="24" id="wait_time" name="wait_time" placeholder="hh" required/>:
                  <input style={inputStyle} min="0" max="59" id="wait_time_minute" name="wait_time_minute" placeholder="mm" required/>:
                  <input style={inputStyle} min="0" max="59" id="wait_time_second" name="wait_time_second" placeholder="ss" required/>
                  <button type="reset" value="Reset">Reset</button>
                </form>
              </div>
            </td>
          </tr>*/}
        </tbody>
        </table>

        <div>
          <button className="generateButton" onClick={this.handleChange}>Generate</button>
        </div>

        {/*<div>
          <b>
            instruction
          </b>
        </div>*/}

        <div>
          <form>
            <input type="text" id="tag" className="generateButton" placeholder="Enter valid PIT tag code"></input>
          </form>
          <h5 id="invalid" className="invalidTag">Valid PIT tag codes contain only numbers 0-9 and letters A-F (not case sensitive)</h5>
          <button className="generateButton" onClick={this.insertTag}>Insert</button>
          <table>
            <tbody>
              <tr >
              <td>
                {tagMapping}
              </td>
              </tr>
            </tbody>
          </table>
          <button className="generateButton" onClick={this.generateTAG}>Generate</button>
        </div>

      </div>
    );
  }
}


export default App;
